.Button {
  font-size: 14px;
  background: #d8b093;
  padding: 6px 12px;
  color: #383838;
  margin: 0 5px;
  min-height: 60px; }
  .Button:focus {
    outline-color: #d8b093; }
  .Button:hover {
    cursor: pointer; }

.FormErr {
  background-color: grey;
  border: 2px solid red; }
  .FormErr:active {
    background-color: red; }



