.Signing {
  display: flex; }
  .Signing .SignIn, .Signing .SignUp {
    font-size: 14px;
    padding: 6px 12px;
    color: white;
    margin: 0 5px;
    min-height: 60px; }
    .Signing .SignIn:focus, .Signing .SignUp:focus {
      outline-color: #d8b093; }
    .Signing .SignIn:hover, .Signing .SignUp:hover {
      cursor: pointer; }
  .Signing .SignIn {
    border: 1px solid transparent;
    background: black; }
  .Signing .SignUp {
    border: 1px solid transparent;
    background: #d8b093; }



