@charset "UTF-8";
.Navigation {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .Navigation .NavigationMenu {
    display: flex; }
    .Navigation .NavigationMenu li {
      position: relative; }
      .Navigation .NavigationMenu li a {
        position: relative;
        color: #383838;
        display: block;
        font-size: 20px;
        padding: 10px 16px;
        font-family: "sofiapro-light"; }
        .Navigation .NavigationMenu li a:hover, .Navigation .NavigationMenu li a:focus {
          font-weight: bold; }
        .Navigation .NavigationMenu li a::before {
          content: "◆";
          position: absolute;
          font-size: 16px;
          left: 0px;
          top: 50%;
          transform: translateY(-50%);
          color: #383838;
          opacity: 0;
          transition: opacity 0.4s; }
        .Navigation .NavigationMenu li a:hover::before {
          opacity: 1; }
    .Navigation .NavigationMenu .Active {
      color: #d8b093; }
      .Navigation .NavigationMenu .Active::before {
        content: "◆";
        position: absolute;
        left: 0px;
        top: 50%;
        transform: translateY(-50%);
        color: #d8b093;
        opacity: 1;
        transition: opacity 0.4s; }

@media screen and (max-width: 920px) {
  .Navigation {
    display: none; } }



